import React, { useState, useEffect, useRef } from "react";
import PropTypes from "prop-types";
import isEqual from "lodash/isEqual";
import Checkbox from "msd-checkbox";
import Button from "msd-button";
import {
  Dropdown,
  DropdownItem,
  DropdownToggle,
  DropdownMenu,
} from "msd-dropdown";

import "./style.scss";

const MultiSelectDropdown = (props) => {
  const {
    menuItems,
    selectedValues,
    disabled,
    className,
    onSearch,
    showSearch,
    selectedValue,
    onApply,
    actionId,
    searchPlaceholder,
    selectAllProps,
    placeholder,
    isApplyEnabled,
    ...attr
  } = props;
  const [isOpen, setOpen] = useState(false);
  const [searchValue, setSearch] = useState("");
  const [selectedItems, setSelectedItems] = useState(
    selectedValues ? [...selectedValues] : []
  );
  const [isBlurStateActive, setBlurState] = useState(true);
  const isSearchEnabled = showSearch && (onSearch || menuItems.length > 5);
  const dropdownRef = useRef(null);

  useEffect(() => {
    if (!isOpen) {
      if (!selectedValues || selectedValues.length === 0) {
        setSelectedItems([]);
      } else if (selectedValues?.length > 0) {
        setSelectedItems([...selectedValues]);
      }
    }
  }, [selectedValues]);

  useEffect(() => {
    if (!isOpen && searchValue) {
      setSearch("");
      if (onSearch) onSearch("");
    }
  }, [isOpen, searchValue]);

  const onMenuItemClick = (value) => {
    const modifiedSelectedItems = selectedItems ? [...selectedItems] : [];
    const index = modifiedSelectedItems.indexOf(value);
    if (index !== -1) modifiedSelectedItems.splice(index, 1);
    else modifiedSelectedItems.push(value);
    setSelectedItems(modifiedSelectedItems);
  };

  const toggle = () => {
    if (isOpen && !isEqual(selectedValues, selectedItems)) {
      setSelectedItems(selectedValues ? [...selectedValues] : []);
      // Handling the onApply logic when the user closes the dropdown only when there is no explicit apply button.
      if (!isApplyEnabled) onApply([...selectedItems]);
    }
    setOpen(!isOpen);
  };

  const getSearchResults = (dataSet) => {
    const searchResult = [];
    const posFirst = [];
    if (searchValue) {
      for (let i = 0; i < dataSet.length; i += 1) {
        let value = dataSet[i].label;
        if (value) {
          value =
            typeof value === "string" ? value.toUpperCase() : value.toString();
          if (value.indexOf(searchValue.toUpperCase()) === 0) {
            posFirst.push(dataSet[i]);
          } else if (value.indexOf(searchValue.toUpperCase()) > 0) {
            searchResult.push(dataSet[i]);
          }
        }
      }
      return [...posFirst, ...searchResult];
    }
    return dataSet;
  };

  const onKeyDown = (event) => {
    // Keycode for handling enter key.
    if (event.keyCode === 13 && searchValue && onSearch) {
      onSearch(searchValue);
    }
  };

  const onClearSearch = () => {
    setSearch("");
    if (onSearch) onSearch("");
  };

  const onTextChange = (event) => {
    const { value } = event.target;
    if (onSearch && searchValue && !value) onSearch("");
    setSearch(value);
  };

  const disableCloseOnBlur = () => {
    setBlurState(false);
  };

  const enableCloseOnBlur = (event) => {
    const relatedTarget = event?.relatedTarget;
    const dropdownEl =
      dropdownRef?.current?.currentRef?.current ||
      dropdownRef?.current?.element;
    if (relatedTarget !== dropdownEl) {
      setBlurState(true);
      toggle();
    } else {
      setBlurState(true);
    }
  };

  const onSelectAllChange = () => {
    if (menuItems.length !== selectedItems.length) {
      const menuItemValues = menuItems.map((item) => item.value);
      const modifiedSelectedItems = [...menuItemValues];
      setSelectedItems(modifiedSelectedItems);
    } else {
      setSelectedItems([]);
    }
  };

  const onApplyClick = () => {
    setSelectedItems(selectedValues ? [...selectedValues] : []);
    onApply([...selectedItems]);
    setOpen(!isOpen);
  };

  const fixedItemsList = menuItems?.filter((item) => item?.isFixed);
  const menuData = getSearchResults(menuItems)?.filter(
    (item) => !item?.isFixed
  );

  const getSelectedValueLabel = (selectedOption) => {
    const selectedOptionObj = menuItems.find(
      (item) => item.value === selectedOption
    );
    return selectedOptionObj?.label;
  };

  return (
    <Dropdown
      isOpen={isOpen}
      toggle={toggle}
      closeMenuOnBlur={false}
      disabled={disabled}
      className={`multiselect ${className}`}
      {...(isBlurStateActive && { onMenuBlur: toggle })}
      data-action-id={`multiselect-${actionId}`}
      ref={dropdownRef}
      {...attr}
    >
      <DropdownToggle caret={true} placeholder={placeholder}>
        {selectedValues?.length > 0 && (
          <div className="selected-item pl-2">
            {selectedValues.length === 1
              ? getSelectedValueLabel(selectedValues[0])
              : `${selectedValues.length} selected`}
          </div>
        )}
      </DropdownToggle>
      <DropdownMenu className="d-flex flex-column justify-content-between py-2">
        {fixedItemsList?.length > 0 && (
          <div className="w-100 fixed-items-container mb-pixel-5 pb-pixel-5 grey-margin">
            {fixedItemsList?.map((item) => (
              <DropdownItem
                key={item.value}
                value={item.value}
                className={`px-3 ${item.className || ""}`}
              >
                <Checkbox
                  checked={selectedItems.includes(item.value)}
                  label={item.label}
                  onChange={() => onMenuItemClick(item.value)}
                />
              </DropdownItem>
            ))}
          </div>
        )}
        {isSearchEnabled && (
          <div className="search-container d-flex align-items-center my-2 mx-3 px-2">
            <img
              src="/static/images/icon/magnifying-glass.svg"
              alt=""
              className="cursor-pointer"
              onClick={onSearch}
              role="presentation"
            />
            <input
              placeholder={searchPlaceholder}
              type="text"
              name="filter"
              className="multi-select-search-box"
              value={searchValue}
              onChange={onTextChange}
              onMouseDown={disableCloseOnBlur}
              onBlur={enableCloseOnBlur}
              autoComplete="off"
              {...(onSearch && { onKeyDown })}
            />
            {searchValue && (
              <img
                src="/static/images/icon/close.svg"
                alt="search"
                height="12px"
                width="12px"
                className="cursor-pointer"
                onClick={onClearSearch}
                role="presentation"
                style={{ opacity: 0.3 }}
              />
            )}
          </div>
        )}
        <div className="multi-select-list-wrapper">
          {selectAllProps && menuData?.length > 0 && (
            <DropdownItem
              key="select-all"
              value="select-all"
              className={`px-3 ${selectAllProps.className || ""}`}
            >
              <Checkbox
                checked={selectedItems.length === menuItems.length}
                label={selectAllProps.label || ""}
                onChange={onSelectAllChange}
              />
            </DropdownItem>
          )}
          {menuData?.length > 0 &&
            menuData?.map((item) => (
              <DropdownItem
                key={item.value}
                value={item.value}
                className={`px-3 ${item.className || ""}`}
              >
                <Checkbox
                  checked={selectedItems.includes(item.value)}
                  label={item.label}
                  onChange={() => onMenuItemClick(item.value)}
                />
              </DropdownItem>
            ))}
          {searchValue && menuData?.length === 0 && (
            <div className="empty-results px-3">No results found!</div>
          )}
        </div>
        {isApplyEnabled && (
          <div class="p-v-1 p-h-2 bdr-t-1-16 bdr-color-4">
            <Button
              className="primary-button my-pixel-20 mx-auto mt-2"
              onMouseDown={(event) => event.preventDefault()}
              onClick={onApplyClick}
              disabled={isEqual(selectedValues, selectedItems)}
              style={{ width: "90%" }}
            >
              Apply
            </Button>
          </div>
        )}
      </DropdownMenu>
    </Dropdown>
  );
};

MultiSelectDropdown.propTypes = {
  actionId: PropTypes.oneOfType([PropTypes.string, PropTypes.number])
    .isRequired,
  menuItems: PropTypes.arrayOf(PropTypes.shape({})),
  selectedValues: PropTypes.arrayOf(PropTypes.string),
  onApply: PropTypes.func.isRequired,
  className: PropTypes.string,
  disabled: PropTypes.bool,
  showSearch: PropTypes.bool,
  searchPlaceholder: PropTypes.string,
  placeholder: PropTypes.string,
  selectedValue: PropTypes.string,
  onSearch: PropTypes.func,
  selectAllProps: PropTypes.shape(),
  isApplyEnabled: PropTypes.bool,
};

MultiSelectDropdown.defaultProps = {
  menuItems: [],
  selectedValues: [],
  selectedValue: "",
  className: "",
  showSearch: true,
  searchPlaceholder: "Search here",
  placeholder: "Select Value",
  disabled: false,
  onSearch: null,
  selectAllProps: null,
  isApplyEnabled: false,
};

export default MultiSelectDropdown;
