import React from "react";
import PropTypes from "prop-types";
import Tooltip from "../tooltip";

const AppliedFilters = (props) => {
  const {
    filters,
    appliedFilters,
    userInfo,
    onResetFilter,
    onClearAllFilters,
    disabledFields,
    isClearEnabled,
  } = props;
  const closeIconStyles = {
    width: 12,
    height: 12,
    backgroundColor: "#8A8EB1",
    maskImage:
      "url(https://sumo-99.github.io/file-host/new_images/filter-close.svg)",
    WebkitMaskImage:
      "url(https://sumo-99.github.io/file-host/new_images/filter-close.svg)",
    WebkitMaskSize: "cover",
    maskSize: "cover",
  };

  return (
    <div className="d-flex mb-pixel-15" style={{ marginLeft: "0px" }}>
      <div className="d-flex-center">
        <div className="font-bold mr-2">Filters</div>
        <div
          role="presentation"
          {...(isClearEnabled && { onClick: onClearAllFilters })}
          className={`clear-all font-bold mr-2 ${
            isClearEnabled ? "cursor-pointer" : ""
          }`}
          style={{
            color: isClearEnabled ? "#059eef" : "#8A8EB1",
            whiteSpace: "nowrap",
          }}
        >
          Clear All
        </div>
        <div
          className="ml-pixel-10"
          style={{ borderLeft: "1px solid #E1E3F4", height: 17 }}
        />
      </div>
      <div className="d-flex align-items-center flex-wrap">
        {(() =>
          appliedFilters.map((item) => {
            let appliedFilterValues = [];
            const filterFieldItem = filters?.find(
              (filterItem) => filterItem?.field === item?.field
            );
            if (!filterFieldItem) return;
            const { field, type, label, value } = filterFieldItem;
            const { values } = item;
            if (
              type === "list" ||
              type === "list-tags" ||
              type === "list-single-select"
            ) {
              // For list types we need to show the label instead of the selectedValue.
              const filterValues = [];
              // eslint-disable-next-line no-unused-expressions
              values?.forEach((selectedValue) => {
                const selectedValueItem = value?.find(
                  (valueItem) => valueItem?.value === selectedValue
                );
                if (selectedValueItem) {
                  filterValues.push({
                    field,
                    type,
                    value: selectedValueItem?.label,
                  });
                }
              });
              appliedFilterValues = appliedFilterValues.concat(filterValues);
            } else if (type === "number-range") {
              appliedFilterValues.push({
                field,
                type,
                value: `${userInfo?.client?.currency || ""} ${values?.[0]} - ${
                  values?.[1]
                }`,
              });
            } else if (type === "checkbox") {
              appliedFilterValues.push({ field, type, value: values[0] });
            }
            const mainSection =
              appliedFilterValues?.length <= 2
                ? appliedFilterValues
                : appliedFilterValues.slice(0, 2);
            const subSection =
              appliedFilterValues?.length <= 2
                ? []
                : appliedFilterValues.slice(2, appliedFilterValues?.length);
            return (
              <div
                key={field}
                role="presentation"
                className="d-flex align-items-center px-2 mt-pixel-5 ml-pixel-10 ellipsis mr-1"
                style={{
                  maxWidth: 550,
                  height: 30,
                  borderRadius: 5,
                  backgroundColor: "#E1E3F4",
                }}
              >
                <div className="mr-1" style={{ color: "#8a8eb1" }}>
                  {`${label}:`}
                </div>
                {mainSection?.map((filter, index) => (
                  <div
                    key={filter?.value}
                    title={filter?.value}
                    className="mr-1"
                  >
                    {`${filter?.value}${
                      index !== mainSection.length - 1 ? "," : ""
                    }`}
                  </div>
                ))}
                {subSection?.length > 0 && (
                  <div className="cursor-pointer">
                    <Tooltip
                      placement="bottom"
                      interactive={true}
                      content={subSection?.map((filter) => (
                        <div key={filter?.value}>{`${filter?.value}`}</div>
                      ))}
                    >
                      <div className="font-bold" style={{ color: "#059eef" }}>
                        {`+${subSection.length} more`}
                      </div>
                    </Tooltip>
                  </div>
                )}
                {!disabledFields.includes(field) && (
                  <img
                    className="cursor-pointer ml-2"
                    style={{ width: "16px", height: "16px" }}
                    onClick={() => onResetFilter(field)}
                    src="https://sumo-99.github.io/file-host/new_images/filter-close.svg"
                  />
                )}
              </div>
            );
          }))()}
      </div>
    </div>
  );
};

//micromaster -> replace line 141 img tag with:
/**
 * <div
                    role="presentation"
                    className="cursor-pointer ml-2"
                    onClick={() => onResetFilter(field)}
                    style={closeIconStyles}
                  />
 */

AppliedFilters.propTypes = {
  filters: PropTypes.arrayOf(PropTypes.shape()),
  appliedFilters: PropTypes.arrayOf(PropTypes.shape()),
  isClearEnabled: PropTypes.bool,
  disabledFields: PropTypes.arrayOf(PropTypes.string),
  onResetFilter: PropTypes.func.isRequired,
  onClearAllFilters: PropTypes.func.isRequired,
  userInfo: PropTypes.shape(),
};

AppliedFilters.defaultProps = {
  filters: [],
  appliedFilters: [],
  userInfo: {},
  disabledFields: [],
  isClearEnabled: true,
};

export default AppliedFilters;
