import React from "react";
import Card from "../card/index";
import { Link, useLocation } from "react-router-dom";
import { Header } from "../header/index";

export function CardTemplate(props) {
  const items = props.items;
  const details = props.details;
  const location = useLocation().pathname;

  return (
    <div>
      <Header />
      <div className="py-3">
        <div className="ml-0">
          <div className="py-2">
            <span className="font-weight-bold py-2">{props.title}</span>
          </div>
          <div className="d-flex flex-row px-0 py-2 ">
            {items.map((item, i) => {
              return (
                <div
                  key={i}
                  className={`ml-0 px-0 mr-4`}
                  // style={{ width: "15rem" }}
                >
                  <Link id="linker" to={`${location}${details[i].location}`}>
                    <Card
                      width="15rem"
                      height="15rem"
                      body={item.body}
                      img={item.url}
                      objectFit={true}
                    />
                  </Link>
                </div>
              );
            })}
          </div>
        </div>
      </div>
    </div>
  );
}
