import React from "react";
import { Link } from "react-router-dom";
import "./card.scss";

function Card(props) {
  const width = props.width;
  const height = props.height;
  const cardImg = props.img;
  const body = props.body;
  let cardStyle;
  if (props.objectFit) {
    cardStyle = { height: height, width: width, objectFit: "cover" };
  } else cardStyle = { maxHeight: height, width: "100%" };
  //can either be maxWidth: "100%" or as above
  const wrapperStyle = { height: height };
  return (
    <div className="card border border-2 border-rad-5">
      <div
        className="img-wrapper"
        style={props.objectFit ? null : wrapperStyle}
      >
        <img className="card-img" src={cardImg} style={cardStyle} />
      </div>
      <div className="my-2 py-1 px-2 font-slight-bold">{body}</div>
    </div>
  );
}

export default Card;
