export function NextArrow(props) {
  const { className, style, onClick } = props;
  return (
    <div
      className={className}
      style={{
        ...style,
        display: "block",
        background: "#4EA3C5",
        borderRadius: "50%",
      }}
      onClick={onClick}
    />
  );
}

