import React from "react";
import PropTypes from "prop-types";
import DropdownList from "../dropdown-list";
import PaginationButton from "./paginationButton";
import "./style.scss";
// import {useTranslation} from "react-i18next";

const Pagination = (props) => {
  const {
    extendedClassName,
    selectedPage,
    totalRecords,
    recordPerPage,
    onPageChange,
    onRecordsPerPageChange,
    recordType,
    dropUp = true,
    recordPerPageOptions = undefined,
    minimal = false,
    displayJumpToPage,
    showTexts = true,
  } = props;

  const totalPages = Math.ceil(totalRecords / recordPerPage);
  // const { t } = useTranslation();
  let menuItems;
  if (recordPerPageOptions) {
    menuItems = recordPerPageOptions;
  } else {
    menuItems = [
      { label: "10", value: 10 },
      { label: "20", value: 20 },
      { label: "30", value: 30 },
      { label: "40", value: 40 },
      { label: "50", value: 50 },
    ];
  }

  const onChange = (value) => {
    let nexPage = value;
    if (value === "pagination_prev") {
      nexPage = selectedPage - 1;
    } else if (value === "pagination_next") {
      nexPage = selectedPage + 1;
    }

    if (value !== "pagination_ellipses") {
      onPageChange(nexPage);
    }
  };

  const onMenuItemClick = (value) => {
    // On changing records per page, selected page should point to 1
    onRecordsPerPageChange(value);
  };

  const jumpToPage = (e) => {
    if (e.key === "Enter") {
      onPageChange(Number(e.target?.value));
    }
  };

  let middleSection = [3, 4, 5];
  if (totalPages <= 7) {
    middleSection = Array.from(
      { length: totalPages - 2 > 0 ? totalPages - 2 : 0 },
      (v, i) => i + 2
    );
  } else if (selectedPage > 4 && selectedPage < totalPages - 2) {
    middleSection = [selectedPage - 1, selectedPage, selectedPage + 1];
  } else if (selectedPage > totalPages - 3) {
    middleSection = [totalPages - 4, totalPages - 3, totalPages - 2];
  }

  const renderPaginationButtons = () => (
    <div className="pagination-wrapper d-flex">
      {/* Left arrow button */}
      <PaginationButton
        type="icon"
        content="pagination_prev"
        isSelected={false}
        isDisabled={selectedPage === 1}
        onChange={onChange}
      />

      {/* first button */}
      <PaginationButton
        type="number"
        content={1}
        isSelected={selectedPage === 1}
        isDisabled={false}
        onChange={onChange}
      />

      {/* buttons at left of the middle buttons */}
      {totalPages > 7 && (
        <PaginationButton
          type={selectedPage < 5 ? "number" : "icon"}
          content={selectedPage < 5 ? 2 : "pagination_ellipses"}
          isSelected={selectedPage === 2}
          isDisabled={false}
          onChange={onChange}
        />
      )}

      {/* buttons at the middle */}
      {middleSection.map((page) => (
        <PaginationButton
          type="number"
          content={page}
          isSelected={selectedPage === page}
          isDisabled={false}
          onChange={onChange}
        />
      ))}

      {/* buttons at right of the middle buttons */}
      {((totalPages > 7 && selectedPage + 2 < totalPages) ||
        (selectedPage >= totalPages - 2 && totalPages > 7)) && (
        <PaginationButton
          type={selectedPage >= totalPages - 3 ? "number" : "icon"}
          content={
            selectedPage >= totalPages - 3
              ? totalPages - 1
              : "pagination_ellipses"
          }
          isSelected={selectedPage === totalPages - 1}
          isDisabled={false}
          onChange={onChange}
        />
      )}

      {/* last button */}
      {totalPages - 1 > 0 && (
        <PaginationButton
          type="number"
          content={totalPages}
          isSelected={selectedPage === totalPages}
          isDisabled={false}
          onChange={onChange}
        />
      )}

      {/* Right arrow button */}
      <PaginationButton
        type="icon"
        content="pagination_next"
        isSelected={false}
        isDisabled={selectedPage === totalPages}
        onChange={onChange}
      />
      {displayJumpToPage && (
        <div className="ml-2 ">
          <input
            style={{ width: "50px", height: "40px" }}
            type="text"
            onKeyDown={jumpToPage}
          />
        </div>
      )}
    </div>
  );

  return (
    <div
      className={`px-5 pagination-panel font-body-2 d-flex align-items-center justify-content-between ${extendedClassName}`}
    >
      <div className="d-flex-center">
        {(!minimal || showTexts) && <span className="mr-2">Showing</span>}
        <DropdownList
          dropdownId="pagination"
          menuItems={menuItems}
          selectedValue={recordPerPage}
          style={{ width: 65 }}
          placeholder="50"
          className="dropdown-list vuex m-t-2"
          onMenuItemClick={onMenuItemClick}
          dropUp={dropUp}
        />
        {showTexts && <span className="ml-2">Records per page</span>}
      </div>
      {renderPaginationButtons()}
      {showTexts ? (
        <div>
          {/* {t("viewing_text", {
            page: `${(selectedPage - 1) * recordPerPage + 1} - ${
              selectedPage === totalPages
                ? totalRecords
                : selectedPage * recordPerPage
            }`,
            totalRecords,
            recordType,
          })} */}
          {/* {`${!minimal?"Viewing":""} ${(selectedPage - 1) * recordPerPage + 1} - ${
              selectedPage === totalPages ? totalRecords : selectedPage * recordPerPage
            } of ${totalRecords} ${recordType}`} */}
        </div>
      ) : (
        <div>
          {`${(selectedPage - 1) * recordPerPage + 1} - ${
            selectedPage === totalPages
              ? totalRecords
              : selectedPage * recordPerPage
          }`}
        </div>
      )}
    </div>
  );
};

Pagination.propTypes = {
  selectedPage: PropTypes.number.isRequired,
  totalRecords: PropTypes.number.isRequired,
  recordPerPage: PropTypes.number,
  onPageChange: PropTypes.func,
  onRecordsPerPageChange: PropTypes.func,
  recordType: PropTypes.string,
  extendedClassName: PropTypes.string,
  displayJumpToPage: PropTypes.bool,
};

Pagination.defaultProps = {
  recordPerPage: 10,
  onPageChange: () => null,
  onRecordsPerPageChange: () => null,
  recordType: "records",
  extendedClassName: "",
  displayJumpToPage: false,
};

export default Pagination;
