import React from 'react';
import PropTypes from 'prop-types';

/**
 * This is a function.
 *
 * @param {boolean} - status
 * @return {string} loader image
 *
 * @example
 *
 *     <Loader status={status} />
 */

function Loader({ status, ...attr }) {
  return status && <div className='loader' data-test-id="loader" {...attr}></div>;
}

Loader.propTypes = {
  status: PropTypes.bool,
};

Loader.defaultProps = {
  status: false,
};
export default React.memo(Loader);
