import React, { useState } from "react";
import "./card.scss";
import Slider from "react-slick";
//imports for arrows of carousel
import { NextArrow, PrevArrow } from "../models/arrows/arrows";

function CarouselCard(props) {
  const width = props.width;
  const height = props.height;
  const img_list = props.images;
  const body = props.body;
  let cardStyle;
  if (props.objectFit) {
    cardStyle = { height: height, width: width, objectFit: "cover" };
  } else cardStyle = { maxHeight: height, width: "100%" };
  //can either be maxWidth: "100%" or as above

  //slider configs
  const [activeSlide, setActiveSlide] = useState(0);

  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    afterChange: (curr) => {
      setActiveSlide(curr);
    },
    customPaging: (i) => {
      return (
        <div
          style={{
            width: "6.5px",
            height: "6.5px",
            backgroundColor: i == activeSlide ? "#059eef" : "#f0f0f2",
            border: "1px #f0f0f2 solid",
            borderRadius: "50%",
          }}
        ></div>
      );
    },
    nextArrow: <NextArrow />,
    prevArrow: <PrevArrow />,
  };
  //end slider configs

  return (
    <div className="card border border-2 border-rad-5 mb-3 mx-3">
      <Slider variableWidth={false} {...settings}>
        {img_list.map((image, i) => {
          return (
            <div key={i} style={{ height: height }}>
              <img
                key={i}
                className="card-img"
                src={image}
                style={{ width: "100%", maxHeight: height }}
              />
            </div>
          );
        })}
      </Slider>
      <div className="my-2 py-1 px-2 font-slight-bold">{body}</div>
    </div>
  );
}

export default CarouselCard;
