import React, { useEffect, useState } from "react";
import Card from "../card/index";

export function AccessoryTemplate(props) {
  //states
  const [data, setData] = useState([]);

  useEffect(() => {
    //get the data from props on component mount
    setData(props.data);
  });

  return (
    <div className="container-fluid mx-4 ">
      <div className="d-flex px-0 flex-wrap">
        {data?.map((item, i) => {
          return (
            <div key={i} className="card-wrapper-4">
              <Card
                key={item.id}
                img={item.accessory_image}
                // height="22rem"
                body={
                  <div>
                    Model {i + 1} - {item.model_name}
                  </div>
                }
              />
            </div>
          );
        })}
        {data.length === 0 ? (
          <div className="justify-content-center align-items-center mt-5 mx-5">
            <h3>No model data available...</h3>
          </div>
        ) : null}
      </div>
    </div>
  );
}
