import React, { useContext, useState } from "react";
import {
  MemoryRouter,
  BrowserRouter,
  Route,
  Routes,
  Link,
  useLocation,
} from "react-router-dom";
import { CardTemplate } from "./components/templates/CardTemplate";
import { ApparelTemplate } from "./components/templates/ApparelTemplate";
import { Accessory } from "./components/Accessory";
import { Model } from "./components/models/index";
import { ModelListContext } from "./contexts/ModelListContext";

import {
  _3DBody,
  homeDetails,
  genderDetails,
  modelDetails,
  homeItems,
  genderItems,
  kidsModelItems,
  maleModelItems,
  femaleModelItems,
  JBody,
  SBody,
  WBody,
  jeweleryItems,
  jeweleryDetails,
} from "./constants";

export default function LazarusRoutes(props) {
  const [models, setModels] = useState([]);
  return (
    <Routes>
      <Route
        path="/"
        exact
        element={
          <CardTemplate
            title="All Categories"
            items={homeItems}
            details={homeDetails}
          />
        }
      />
      <Route
        path="/apparel"
        exact
        element={
          <CardTemplate
            title="Apparel"
            items={genderItems}
            details={genderDetails}
          />
        }
      />
      <Route
        path="/accessories"
        exact
        element={
          <CardTemplate
            title="Accessories"
            items={jeweleryItems}
            details={jeweleryDetails}
          />
        }
      />
      <Route
        path="/bags"
        exact
        element={
          <CardTemplate
            title="Bags"
            items={genderItems}
            details={genderDetails}
          />
        }
      />
      <Route
        path="/accessories/jewelery"
        exact
        element={
          <CardTemplate
            title="Jewelery"
            items={genderItems}
            details={genderDetails}
          />
        }
      />
      <Route
        path="/accessories/sunglasses"
        exact
        element={
          <CardTemplate
            title="Sunglasses"
            items={genderItems}
            details={genderDetails}
          />
        }
      />
      <Route
        path="/accessories/watches"
        exact
        element={
          <CardTemplate
            title="Watches"
            items={genderItems}
            details={genderDetails}
          />
        }
      />
      <Route
        path="/accessories/jewelery/male"
        exact
        element={<Accessory gender="male" />}
      />
      <Route
        path="/accessories/jewelery/female"
        exact
        element={<Accessory category="jewelery" gender="female" />}
      />
      <Route
        path="/accessories/jewelery/kids"
        exact
        element={<Accessory gender="kids" />}
      />
      <Route
        path="/accessories/sunglasses/male"
        exact
        element={
          <CardTemplate
            title="Sunglasses"
            items={maleModelItems}
            details={modelDetails}
          />
        }
      />
      <Route
        path="/accessories/sunglasses/female"
        exact
        element={
          <CardTemplate
            title="Sunglasses"
            items={femaleModelItems}
            details={modelDetails}
          />
        }
      />
      <Route
        path="/accessories/sunglasses/kids"
        exact
        element={
          <CardTemplate
            title="Sunglasses"
            items={kidsModelItems}
            details={modelDetails}
          />
        }
      />
      <Route
        path="/accessories/watches/male"
        exact
        element={
          <CardTemplate
            title="Watches"
            items={maleModelItems}
            details={modelDetails}
          />
        }
      />
      <Route
        path="/accessories/watches/female"
        exact
        element={
          <CardTemplate
            title="Watches"
            items={femaleModelItems}
            details={modelDetails}
          />
        }
      />
      <Route
        path="/accessories/watches/kids"
        exact
        element={
          <CardTemplate
            title="Watches"
            items={kidsModelItems}
            details={modelDetails}
          />
        }
      />
      <Route
        path="/apparel/male"
        exact
        element={
          <CardTemplate
            title="Male"
            items={maleModelItems}
            details={modelDetails}
          />
        }
      />
      <Route
        path="/apparel/female"
        exact
        element={
          <CardTemplate
            title="Female"
            items={femaleModelItems}
            details={modelDetails}
          />
        }
      />
      <Route
        path="/apparel/kids"
        exact
        element={
          <CardTemplate
            title="Kids"
            items={kidsModelItems}
            details={modelDetails}
          />
        }
      />
      <Route
        path="/apparel/male/MSD_MODELS"
        exact
        element={
          <ModelListContext.Provider value={{ models, setModels }}>
            <ApparelTemplate gender="male" />
          </ModelListContext.Provider>
        }
      />
      <Route
        path="/apparel/female/MSD_MODELS"
        exact
        element={
          <ModelListContext.Provider value={{ models, setModels }}>
            <ApparelTemplate gender="female" />
          </ModelListContext.Provider>
        }
      />
      <Route
        path="/apparel/female/MSD_MODELS/:id"
        exact
        element={
          <ModelListContext.Provider value={{ models, setModels }}>
            <Model
              gender="female"
              shoes="shoes"
              hairstyle="hairstyle"
              all="all"
            />
          </ModelListContext.Provider>
        }
      />
      <Route
        path="/apparel/male/MSD_MODELS/:id"
        exact
        element={
          <ModelListContext.Provider value={{ models, setModels }}>
            <Model gender="male" />
          </ModelListContext.Provider>
        }
      />
      <Route
        path="/apparel/kids/MSD_MODELS"
        exact
        element={<ApparelTemplate gender="kids" />}
      />
      <Route
        path="/apparel/male/ICM_MODELS"
        exact
        element={<ApparelTemplate gender="male" />}
      />
      <Route
        path="/apparel/female/ICM_MODELS"
        exact
        element={<ApparelTemplate gender="female" />}
      />
      <Route
        path="/apparel/kids/ICM_MODELS"
        exact
        element={<ApparelTemplate gender="kids" />}
      />
      <Route
        path="/apparel/male/3D_MODELS"
        exact
        element={<ApparelTemplate gender="male" />}
      />
      <Route
        path="/apparel/female/3D_MODELS"
        exact
        element={<ApparelTemplate gender="female" />}
      />
      <Route
        path="/apparel/kids/3D_MODELS"
        exact
        element={<ApparelTemplate gender="kids" />}
      />
      <Route
        path="/bags/male"
        exact
        element={
          <CardTemplate
            title="Male"
            items={maleModelItems}
            details={modelDetails}
          />
        }
      />
      <Route
        path="/bags/female"
        exact
        element={
          <CardTemplate
            title="Female"
            items={femaleModelItems}
            details={modelDetails}
          />
        }
      />
      <Route
        path="/bags/kids"
        exact
        element={
          <CardTemplate
            title="Kids"
            items={kidsModelItems}
            details={modelDetails}
          />
        }
      />
    </Routes>
  );
}
