import React, { useState, useEffect, useContext } from "react";
import { useParams, useLocation, useNavigate, Link } from "react-router-dom";
import "./header.scss";

export function Header(props) {
  let nav = useNavigate();
  let loc = useLocation();
  let location = loc.pathname;

  let { id } = useParams();
  id = parseInt(id);
  let location2 = location;
  location2 = location2
    .split("/")
    .slice(0, -1)
    .join("/");

  //image constants
  const back = "https://sumo-99.github.io/file-host/new_images/back.svg";
  const next = "https://sumo-99.github.io/file-host/new_images/next.svg";
  const previous =
    "https://sumo-99.github.io/file-host/new_images/previous.svg";
  //states
  const [pathArr, setPathArr] = useState([]);
  const [displayBack, setDisplayBack] = useState(false);
  const modelList = props.modelList;
  const [nextStyle, setNextStyle] = useState("custom-btn");
  const [prevStyle, setPrevStyle] = useState("custom-btn");
  const [prevDisabled, setPrevDisabled] = useState(false);
  const [nextDisabled, setNextDisabled] = useState(false);

  useEffect(() => {
    if (location === "/") setDisplayBack(false);
    else setDisplayBack(true);
    changeTitle();
  }, [location]);

  useEffect(() => {
    if (modelList) {
      let temp_idx = modelList.indexOf(id);
      if (temp_idx == modelList.length - 1) {
        setNextDisabled(true);
        setNextStyle("custom-btn-disabled");
      } else {
        setNextDisabled(false);
        setNextStyle("custom-btn");
      }
      if (temp_idx == 0) {
        setPrevDisabled(true);
        setPrevStyle("custom-btn-disabled");
      } else {
        setPrevDisabled(false);
        setPrevStyle("custom-btn");
      }
    }
  }, [modelList, loc]);

  //util functions to prepare the breadcumb elements
  const capitalize = (string) => {
    if (string == "") return "Model Library";
    if (string.includes("_")) return string.replace("_", " ");
    return string.charAt(0).toUpperCase() + string.slice(1);
  };

  const makeURL = (idx) => {
    let url = "";
    for (let i = 0; i <= idx; i++) {
      url += pathArr[i] + "/";
    }
    url = url.slice(0, -1);
    return url;
  };

  const changeTitle = () => {
    let paths = location.split("/");
    setPathArr(paths);
  };

  const makeTitle = () => {
    if (!props.title) {
      if (pathArr.length > 0) return capitalize(pathArr[pathArr.length - 1]);
    }
    return <div className="">{props.title}</div>;
  };

  return (
    <div>
      <div className="border-bottom border-breadcrumb border-5 pt-5">
        <nav>
          <ul className="bc">
            {pathArr.map((item, idx) => {
              if (idx != pathArr.length - 1) {
                if (idx == 0)
                  return (
                    <li key={idx} className="text-primary">
                      <Link id="linker" className="text-primary" to={`/`}>
                        {capitalize(item)}
                      </Link>
                    </li>
                  );
                else
                  return (
                    <li key={idx} className="">
                      <Link
                        // id="linker"
                        className="light"
                        to={`${makeURL(idx)}`}
                      >
                        {capitalize(item)}
                      </Link>
                    </li>
                  );
              }
            })}
          </ul>
          <div className="px-0 mb-2">
            <div className="d-flex justify-content-between align-items-center pb-1 px-0">
              <div className="d-flex-start justify-content-between align-items-center  ">
                <div className="d-flex align-items-center">
                  {displayBack ? (
                    <div>
                      <img
                        src={back}
                        className="mr-2"
                        onClick={() => nav(-1)}
                        role="button"
                        style={{ width: "1.5rem", height: "1.5rem" }}
                      />
                    </div>
                  ) : null}
                  <div className="text-center mb-1">{makeTitle()}</div>
                </div>
              </div>
              {modelList ? (
                <div className="d-flex-end pb-2">
                  <Link
                    id="linker"
                    className={prevDisabled ? "disabled-link" : ""}
                    to={`${location2}/${modelList[modelList.indexOf(id) - 1]}`}
                  >
                    <div id="btn" className={prevStyle} role="button">
                      <img
                        src={previous}
                        className="pr-2"
                        style={{ paddingTop: "0.2rem" }}
                      />
                      <div className="font-weight-bold">Previous</div>
                    </div>
                  </Link>
                  <div className="mr-2"></div>
                  <Link
                    id="linker"
                    className={nextDisabled ? "disabled-link" : ""}
                    to={`${location2}/${modelList[modelList.indexOf(id) + 1]}`}
                  >
                    <div id="btn" className={nextStyle} role="button">
                      <div className="font-weight-bold">Next</div>
                      <img
                        src={next}
                        className="pl-2"
                        style={{ paddingTop: "0.2rem" }}
                      />
                    </div>
                  </Link>
                </div>
              ) : (
                <div></div>
              )}
            </div>
          </div>
        </nav>
      </div>
    </div>
  );
}
