import React, { useState, useRef } from "react";
import PropTypes from "prop-types";
import {
  Dropdown,
  DropdownItem,
  DropdownToggle,
  DropdownMenu,
} from "msd-dropdown";
import Button from "msd-button";

import "./style.scss";

const DropdownList = (props) => {
  const {
    selectedValue,
    menuItems,
    placeholder,
    disabled,
    showCaret,
    showSearch,
    autoWidth,
    dropdownId,
    toggle,
    className,
    innerLabel,
    onSearch,
    searchPlaceholder,
    style,
    closeMenuOnBlur = true,
    addItemEnabled,
    onAddOption,
    addOptionText,
    dropUp = false,
  } = props;

  const [isOpen, setOpen] = useState(false);
  const [isBlur, setBlur] = useState(true);
  const [searchValue, setSearch] = useState("");
  const isSearchEnabled = showSearch || onSearch || menuItems.length > 5;
  const dropdownRef = useRef(null);

  const getValue = () => {
    const item = menuItems.find((el) => el.value === selectedValue);
    return item && item.label ? item.label : "";
  };

  const selectedDropdownValue = innerLabel
    ? `${innerLabel} ${getValue()}`
    : getValue();

  const addOption = () => {
    onToggle();
    onAddOption(searchValue);
  };

  const onToggle = () => {
    setOpen(!isOpen);
    setSearch("");
    if (toggle) toggle(!isOpen);
  };

  const onMenuItemClick = (value, attributes) => {
    onToggle();
    props.onMenuItemClick(value, attributes);
  };

  const getSearchResults = (dataSet) => {
    const searchResult = [];
    const posFirst = [];

    if (searchValue && searchValue !== "") {
      for (let i = 0; i < dataSet.length; i += 1) {
        const value = dataSet[i].label;
        if (value.toUpperCase().indexOf(searchValue.toUpperCase()) === 0) {
          posFirst.push(dataSet[i]);
        } else if (value.toUpperCase().indexOf(searchValue.toUpperCase()) > 0) {
          searchResult.push(dataSet[i]);
        }
      }
      return [...posFirst, ...searchResult];
    }
    return dataSet;
  };

  const disableCloseOnBlur = () => setBlur(false);

  const enableCloseOnBlur = (event) => {
    const { relatedTarget } = event;
    const dropdownEl =
      dropdownRef.current.currentRef.current || dropdownRef.current.element;
    if (relatedTarget !== dropdownEl) {
      setBlur(true);
      onToggle();
    } else {
      setBlur(true);
    }
  };

  const onKeyDown = (event) => {
    // Keycode for handling enter key.
    if (event.keyCode === 13 && searchValue && onSearch) {
      onSearch(searchValue);
    }
  };

  const onClearSearch = () => {
    setSearch("");
    if (onSearch) onSearch("");
  };

  const onTextChange = (event) => {
    const { value } = event.target;
    if (onSearch && searchValue && !value) onSearch("");
    setSearch(value);
  };

  const fixedItemsList = menuItems.filter((item) => item.isFixed);
  const menuData = getSearchResults(menuItems).filter((item) => !item.isFixed);

  return (
    <Dropdown
      isOpen={isOpen}
      toggle={onToggle}
      disabled={disabled}
      className={`${className} ${dropUp && "drop-up"}`}
      style={style}
      autoWidth={autoWidth}
      closeMenuOnBlur={closeMenuOnBlur}
      data-action-id={`dropdownList-${dropdownId}`}
      {...(isBlur && { onMenuBlur: onToggle })}
      ref={dropdownRef}
    >
      <DropdownToggle caret={showCaret} placeholder={placeholder}>
        {selectedDropdownValue && (
          <div className="selected-item pl-2">{selectedDropdownValue}</div>
        )}
      </DropdownToggle>
      <DropdownMenu className="d-flex flex-column justify-content-between py-2">
        {fixedItemsList.length > 0 && (
          <div
            className={`w-100 fixed-items-container
              ${fixedItemsList.length >= 2 ? "pb-pixel-5 grey-margin" : ""}`}
          >
            {fixedItemsList.map((el) => (
              <DropdownItem
                key={el.value}
                value={el.value}
                onClick={onMenuItemClick}
                className={`px-3 ${
                  selectedValue === el.value ? "dropdown-selected" : ""
                } ${el.className || ""}`}
                disabled={el.disabled}
              >
                {el.label}
                {el.suffix || null}
              </DropdownItem>
            ))}
          </div>
        )}
        {isSearchEnabled && (
          <div className="search-container d-flex align-items-center my-2 mx-3 px-2">
            <img
              src="/static/images/icon/magnifying-glass.svg"
              alt=""
              className="cursor-pointer"
              onClick={onSearch}
              role="presentation"
            />
            <input
              placeholder={searchPlaceholder}
              type="text"
              name="filter"
              className="dropdown-list-search-box"
              value={searchValue}
              onChange={onTextChange}
              onMouseDown={disableCloseOnBlur}
              onBlur={enableCloseOnBlur}
              autoComplete="off"
              {...(onSearch && { onKeyDown })}
            />
            {searchValue && (
              <img
                src="/static/images/icon/close.svg"
                alt="search"
                height="12px"
                width="12px"
                className="cursor-pointer"
                onClick={onClearSearch}
                role="presentation"
                style={{ opacity: 0.3 }}
              />
            )}
          </div>
        )}
        <div data-test-id="drop-down-option" className="dropdown-list-wrapper">
          {menuData.length > 0 &&
            menuData.map((el) => (
              <DropdownItem
                key={el.value}
                value={el.value}
                onClick={onMenuItemClick}
                className={`px-3 ${
                  selectedValue === el.value ? "dropdown-selected" : ""
                } ${el.className || ""}`}
                disabled={el.disabled}
              >
                {el.label}
                {el.suffix || null}
              </DropdownItem>
            ))}
          {!addItemEnabled && searchValue && menuData.length === 0 && (
            <div className="empty-results px-3">No results found!</div>
          )}
          {addItemEnabled && searchValue && menuData.length === 0 && (
            <div
              className="my-2 mx-3 color-1 font-bold pointer"
              onClick={addOption}
            >
              {addOptionText}
            </div>
          )}
        </div>
      </DropdownMenu>
    </Dropdown>
  );
};

DropdownList.propTypes = {
  dropdownId: PropTypes.oneOfType([PropTypes.string, PropTypes.number])
    .isRequired,
  menuItems: PropTypes.arrayOf(PropTypes.shape({})),
  selectedValue: PropTypes.oneOfType([
    PropTypes.string,
    PropTypes.number,
    PropTypes.bool,
  ]),
  placeholder: PropTypes.string,
  onMenuItemClick: PropTypes.func,
  toggle: PropTypes.func,
  disabled: PropTypes.bool,
  autoWidth: PropTypes.bool,
  className: PropTypes.string,
  style: PropTypes.shape({}),
  showCaret: PropTypes.bool,
  showSearch: PropTypes.bool,
  innerLabel: PropTypes.string,
  searchPlaceholder: PropTypes.string,
  onSearch: PropTypes.func,
  onAddOption: PropTypes.func,
  addOptionText: PropTypes.string,
};

DropdownList.defaultProps = {
  menuItems: [],
  selectedValue: "",
  placeholder: "",
  innerLabel: "",
  onMenuItemClick: (e) => e,
  toggle: (e) => e,
  disabled: false,
  autoWidth: true,
  className: "",
  style: {},
  showCaret: true,
  showSearch: false,
  addItemEnabled: false,
  onSearch: null,
  searchPlaceholder: "Search here",
};

export default DropdownList;
