import React, { useState } from "react";
import ReactDOM from "react-dom";
import Button from "msd-button";
import isEqual from "lodash/isEqual";
import PropTypes from "prop-types";
import { Modal, ModalBody, ModalHeader } from "msd-modal";
import MultiSelectDropdown from "../multiselect-dropdown";
import AppliedFilters from "./appliedFilters";
import Tooltip from "../tooltip";
import "./style.scss";
import DropdownList from "../dropdown-list";
import Loader from "../Loader";
// import { useTranslation } from "react-i18next";
import Checkbox from "msd-checkbox";
import { deepCopy } from "../../vuecomponents/helpers/generics";
import filter from "../../../images/filter.svg";

const Filters = (props) => {
  const {
    filters,
    appliedFilters,
    setAppliedFilters,
    onApplyFilter,
    selectedFilters,
    appliedFiltersRef,
    setSelectedFilters,
    onSearch,
    onCancelFilter,
    className,
    userInfo,
    disabledFields,
    filterLoader = false,
  } = props;
  const [isFiltersModalOpen, toggleFiltersModal] = useState(false);

  const handleApplyFilter = () => {
    toggleFiltersModal(!isFiltersModalOpen);
    setAppliedFilters(selectedFilters);
    onApplyFilter(selectedFilters);
  };

  // const { t } = useTranslation();
  const onClearSelectedFilters = () => {
    const modifiedFilters = selectedFilters.filter((filter) =>
      disabledFields.includes(filter.field)
    );
    setSelectedFilters(modifiedFilters);
  };

  const onClearAllFilters = () => {
    const modifiedFilters = appliedFilters.filter((filter) =>
      disabledFields.includes(filter.field)
    );
    setSelectedFilters(modifiedFilters);
    setAppliedFilters(modifiedFilters);
    onApplyFilter(modifiedFilters);
  };

  const onCancel = () => {
    setSelectedFilters(appliedFilters);
    toggleFiltersModal(false);
    if (onCancelFilter) onCancelFilter();
  };

  const onValueChange = (event, filter, range) => {
    const { field } = filter;
    const { value } = event.target;
    const currentFilterField = selectedFilters.find(
      (item) => item.field === filter.field
    );
    let filterField = currentFilterField ? { ...currentFilterField } : null;
    if (filterField) {
      const rangeValues = [...filterField.values];
      rangeValues[range === "min" ? 0 : 1] = value || null;
      filterField.values = rangeValues;
      let modifiedFilters;
      if (filterField.values.every((el) => el === null)) {
        modifiedFilters = selectedFilters.filter(
          (item) => item.field !== field
        );
      } else {
        modifiedFilters = selectedFilters.map((item) => {
          if (item.field === field) return { ...item, ...filterField };
          return item;
        });
      }
      setSelectedFilters(modifiedFilters);
    } else {
      filterField = {
        ...filter,
        values: range === "min" ? [value, null] : [null, value],
      };
      setSelectedFilters([...selectedFilters, filterField]);
    }
  };

  const onCheckBoxValueChange = (value, filter) => {
    let tempSelectedFilters = deepCopy(selectedFilters);
    const currentFilterField = tempSelectedFilters.find(
      (item) => item.field === filter.field
    );
    if (currentFilterField) {
      currentFilterField.values[0] = !currentFilterField.values[0];
    } else {
      const filterField = { ...filter, values: [value] };
      tempSelectedFilters.push(filterField);
    }
    setSelectedFilters([...tempSelectedFilters]);
  };

  const validateProductsCount = (event) => {
    const keycode = event.charCode || event.keyCode;
    if (keycode === 45) {
      event.preventDefault();
    }
  };

  const updateSelectedFilters = (filter, values) => {
    const { field } = filter;
    const currentFilterField = selectedFilters.find(
      (item) => item.field === field
    );
    let filterField = currentFilterField ? { ...currentFilterField } : null;
    let modifiedFilters = [];
    if (values.length > 0) {
      if (filterField) {
        filterField.values = values;
        modifiedFilters = selectedFilters.map((item) => {
          if (item.field === field) return { ...item, ...filterField };
          return item;
        });
      } else {
        filterField = { ...filter, values };
        modifiedFilters.push(filterField);
        modifiedFilters = [...selectedFilters, ...modifiedFilters];
      }
    } else {
      modifiedFilters = selectedFilters.filter((item) => item.field !== field);
    }
    setSelectedFilters(modifiedFilters, field);
  };

  const getSelectedFilters = (field, type = "multiple") => {
    const selectedFilter = selectedFilters.find(
      (filter) => filter.field === field
    );
    return selectedFilter
      ? type === "single"
        ? selectedFilter.values[0]
        : selectedFilter.values
      : [];
  };

  const onResetFilterItem = (filter) => {
    const modifiedFilters = selectedFilters.filter(
      (item) => item.field === filter.field
    );
    setSelectedFilters(modifiedFilters);
  };

  const onResetFilter = (field) => {
    const modifiedFilters = appliedFilters.filter(
      (item) => item.field !== field
    );
    setSelectedFilters(modifiedFilters, field);
    setAppliedFilters(modifiedFilters, field);
    onApplyFilter(modifiedFilters, field);
  };

  const onRemoveSelectedTagItem = (itemIndex, filterItemData, values) => {
    const modifiedValues = [...values];
    modifiedValues.splice(itemIndex, 1);
    updateSelectedFilters(filterItemData, modifiedValues);
  };

  const renderFilterItem = (filter) => {
    const { field, label, values, type, help } = filter;
    const isDisabled = disabledFields.includes(field);
    return (
      <div className="mt-4" key={field}>
        <div className="d-flex align-items-center mb-1 font-bold filters-field-label">
          {`${label}`}
          {help ? (
            <Tooltip
              content={help}
              placement="bottom-start"
              className="filters-help-text cursor-pointer"
            >
              <img
                src="static/images/icon/path.svg"
                alt="help text"
                className="mx-2 filters-help-icon"
              />
            </Tooltip>
          ) : null}
        </div>
        {(() => {
          if (type === "list") {
            let filterValues = values;
            if (filter.searchText) {
              filterValues = filter.searchResults || [];
            }
            // TODO: Need to integrate else part while passing in the onSearch functionality same as vueApp.
            filterValues = filterValues?.length > 0 ? filterValues : [];
            return (
              <MultiSelectDropdown
                menuItems={filterValues}
                selectedValues={getSelectedFilters(field)}
                placeholder="Select"
                actionId={field}
                selectAllProps={{ label: "Select all" }}
                disabled={isDisabled}
                onReset={() => onResetFilterItem(filter)}
                onApply={(selectedValues) =>
                  updateSelectedFilters(filter, selectedValues)
                }
                className={`vuex filters-multiselect pos-relative ${
                  isDisabled ? "disabled-multiselect" : ""
                }`}
                {...(onSearch && {
                  onSearch: (searchText) => onSearch(field, searchText),
                })}
              />
            );
          }
          if (type === "list-single-select") {
            let filterValues = values;
            if (filter.searchText) {
              filterValues = filter.searchResults || [];
            }
            // TODO: Need to integrate else part while passing in the onSearch functionality same as vueApp.
            filterValues = filterValues?.length > 0 ? filterValues : [];
            return (
              <DropdownList
                showSearch={filter.showSearch || false}
                menuItems={filterValues}
                style={{
                  height: "40px",
                  minWidth: "150px",
                  backgroundColor: "white",
                }}
                selectedValue={getSelectedFilters(field, "single")}
                placeholder={`Select ${filter.label} `}
                onMenuItemClick={(selectedValue) =>
                  updateSelectedFilters(filter, [selectedValue])
                }
                className="dropdown-list vuex"
                closeMenuOnBlur={filter.closeMenuOnBlur}
                searchPlaceholder="Search Here"
              />
            );
          }
          if (type === "list-tags") {
            let filterValues = values;
            if (filter.searchText) {
              filterValues = filter.searchResults || [];
            }
            // TODO: Need to integrate else part while passing in the onSearch functionality same as vueApp.
            filterValues = filterValues?.length > 0 ? filterValues : [];
            const selectedFilterValues = getSelectedFilters(field);
            return (
              <div className="full-width">
                <MultiSelectDropdown
                  menuItems={filterValues}
                  selectedValues={selectedFilterValues}
                  placeholder="Select"
                  actionId={field}
                  selectAllProps={{ label: "Select all" }}
                  disabled={isDisabled}
                  onReset={() => onResetFilterItem(filter)}
                  onApply={(selectedValues) =>
                    updateSelectedFilters(filter, selectedValues)
                  }
                  className={`filters-multiselect pos-relative mb-2 ${
                    isDisabled ? "disabled-multiselect" : ""
                  }`}
                  {...(onSearch && {
                    onSearch: (searchText) => onSearch(field, searchText),
                  })}
                />
                {selectedFilterValues.length > 0 ? (
                  <div className="d-flex flex-wrap">
                    {selectedFilterValues.map((tagName, index) => (
                      <div
                        className="list-tag mr-2 d-flex px-2 py-1 mt-1 align-items-center bdr-rad-4-16"
                        style={{ backgroundColor: "e1e3f4", color: "#4A4A4A" }}
                      >
                        <div className="mr-1">{tagName}</div>
                        <div
                          role="presentation"
                          className="filter-tag-close cursor-pointer ml-2"
                          onClick={() =>
                            onRemoveSelectedTagItem(
                              index,
                              filter,
                              selectedFilterValues
                            )
                          }
                        />
                      </div>
                    ))}
                  </div>
                ) : (
                  <div className="font-body-1 mt-2 ml-1">
                    No Values Selected
                  </div>
                )}
              </div>
            );
          }
          if (type === "number-range") {
            return (
              <div className="d-flex full-width">
                <input
                  type="number"
                  className="filters-input mr-2"
                  placeholder="Min"
                  min="0"
                  onKeyPress={validateProductsCount}
                  value={getSelectedFilters(field)[0] || ""}
                  onChange={(event) => onValueChange(event, filter, "min")}
                />
                <input
                  type="number"
                  className="filters-input ml-2"
                  placeholder="Max"
                  min="0"
                  onKeyPress={validateProductsCount}
                  value={getSelectedFilters(field)[1] || ""}
                  onChange={(event) => onValueChange(event, filter, "max")}
                />
              </div>
            );
          }

          if (type === "checkbox") {
            const value = getSelectedFilters(field)[0];
            return (
              <div className="d-flex full-width">
                <Checkbox
                  onChange={(val) => {
                    onCheckBoxValueChange(val.currentTarget.checked, filter);
                  }}
                  checked={value}
                />
              </div>
            );
          }
          return null;
        })()}
      </div>
    );
  };

  const isClearEnabled = (entity) =>
    entity?.filter((filter) => !disabledFields.includes(filter?.field))
      ?.length > 0;
  const isSelectedClearEnabled = isClearEnabled(selectedFilters);
  const isApplyClearEnabled = isClearEnabled(appliedFilters);

  const renderFiltersModal = () => (
    <Modal
      isOpen={isFiltersModalOpen}
      toggle={toggleFiltersModal}
      showCloseIcon={false}
      className="filters-modal"
    >
      <ModalHeader className="px-4">
        <div className="d-flex align-items-center justify-content-between">
          <div
            className="font-bold font-body-3 color-7"
            style={{ fontWeight: "bold" }}
          >
            Add Filters
          </div>
          <div className="d-flex-center">
            <Button className="mr-2 cancel-button font-bold" onClick={onCancel}>
              Cancel
            </Button>
            <Button
              className="primary-button"
              onClick={handleApplyFilter}
              disabled={isEqual(selectedFilters, appliedFilters)}
              style={{ height: 40, width: 80 }}
            >
              Apply
            </Button>
          </div>
        </div>
      </ModalHeader>
      <ModalBody style={{ height: "100%" }} className="p-0">
        <div
          role="presentation"
          className={`${
            isSelectedClearEnabled ? "cursor-pointer" : ""
          } font-bold flt-r my-2 mr-4`}
          {...(isSelectedClearEnabled && { onClick: onClearSelectedFilters })}
          style={{ color: isSelectedClearEnabled ? "#059EEF" : "#9b9b9b" }}
        >
          Clear All Filters
        </div>
        <div
          className="full-content px-4"
          style={{ minHeight: "100%", height: `${filters.length * 22}%` }}
        >
          {filters?.map(renderFilterItem)}
          <Loader status={filterLoader} />
        </div>
      </ModalBody>
    </Modal>
  );

  return (
    <div className={`filters d-flex align-items-center ${className}`}>
      <div
        role="presentation"
        className={`add-filter d-flex-center px-1 cursor-pointer ${
          appliedFilters.length > 0 ? "filters-active" : ""
        }`}
        onClick={() => toggleFiltersModal(!isFiltersModalOpen)}
      >
        <img src="https://sumo-99.github.io/file-host/new_images/filter.svg" height={15} width={15} alt="" className="mr-2" />
        <div>Filters</div>
      </div>
      {appliedFiltersRef &&
        appliedFilters?.length > 0 &&
        ReactDOM.createPortal(
          <AppliedFilters
            filters={filters}
            onResetFilter={onResetFilter}
            onClearAllFilters={onClearAllFilters}
            appliedFilters={appliedFilters}
            userInfo={userInfo}
            disabledFields={disabledFields}
            isClearEnabled={isApplyClearEnabled}
            // t={t}
          />,
          appliedFiltersRef
        )}
      {isFiltersModalOpen && renderFiltersModal()}
    </div>
  );
};

Filters.propTypes = {
  filters: PropTypes.arrayOf(PropTypes.shape({})),
  disabledFields: PropTypes.arrayOf(PropTypes.string),
  appliedFiltersRef: PropTypes.shape(),
  appliedFilters: PropTypes.arrayOf(PropTypes.shape({})),
  selectedFilters: PropTypes.arrayOf(PropTypes.shape({})),
  setSelectedFilters: PropTypes.func.isRequired,
  onApplyFilter: PropTypes.func.isRequired,
  setAppliedFilters: PropTypes.func.isRequired,
  onSearch: PropTypes.func,
  onCancelFilter: PropTypes.func,
  userInfo: PropTypes.shape({}),
  className: PropTypes.string,
};

Filters.defaultProps = {
  filters: [],
  disabledFields: [],
  onSearch: null,
  appliedFiltersRef: null,
  userInfo: {},
  selectedFilters: [],
  appliedFilters: [],
  onCancelFilter: null,
  className: "",
};

export default Filters;
