import React from "react";
import PropTypes from "prop-types";
const PaginationButton = (props) => {
  const { type, content, isSelected, isDisabled, onChange } = props;

  return (
    <div
      className={`pagination-button d-flex-center
        ${type}${isSelected ? "-selected" : ""} ${isDisabled ? "disabled" : ""}
        ${content === "pagination_ellipses" ? "ellipses" : ""}`}
      role="presentation"
      onClick={!isDisabled ? () => onChange(content) : undefined}
      key={`${type}-${content}`}
    >
      {type === "number" ? (
        content
      ) : (
        <img
          src={`https://dev-vuemodel.madstreetden.com/static/images/icon/${content}_${
            isDisabled ? "disabled" : "enabled"
          }.svg`}
          alt=""
          className="button-icon"
        />
      )}
    </div>
  );
};

PaginationButton.propTypes = {
  type: PropTypes.string.isRequired,
  content: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
  isSelected: PropTypes.bool.isRequired,
  isDisabled: PropTypes.bool.isRequired,
  onChange: PropTypes.func.isRequired,
};

export default PaginationButton;
