export const getModelList = async (params) => {
  let APIURL =
    "https://dev-api-vuemodel.madstreetden.com/api/v0.1/model_library";
  try {
    let response = await Axios({
      method: "get",
      url: APIURL,
      responseType: "json",
      params,
      // headers: getApiHeaders(),
    });
    if (response.status === 200) {
      let models = response.data.results || [];
      let totalCount = response.data.count || 0;
      return { models, totalCount };
    } else {
      throw new Error();
    }
  } catch (err) {
    return { models: [] };
  }
};

export const getModelDetails = async (params, id) => {
  let APIURL = `https://dev-api-vuemodel.madstreetden.com/api/v0.1/model_library_details/${id}`;
  try {
    let response = await Axios({
      method: "get",
      url: APIURL,
      responseType: "json",
      params,
      // headers: getApiHeaders(),
    });

    if (response.status === 200) {
      let details = response.data || [];
      return { details };
    } else {
      throw new Error();
    }
  } catch (err) {
    return { details: [] };
  }
};

