import React, { useState } from "react";
import "./sidebar.scss";

const mapper = {
  Necklace: "necklace",
  "Bracelet or rings": "bracelet",
  Earrings: "earrings",
  All: "all",
  Shoes: "shoes",
  "Hair Style": "hair",
};

export function SidebarBox(props) {
  return (
    <div
      className={`${props.active == true ? "bg-sbox" : ""} s-box-wrapper py-2`}
    >
      <div className="s-box ">
        <div
          className={`${
            props.active
              ? `${mapper[props.name]}-white`
              : `${mapper[props.name]}`
          } s-icon px-2`}
        />
        <div
          className={`${
            props.active == true ? "text-light" : "text-dark"
          } s-name text-center`}
        >
          {props.name}
        </div>
      </div>
    </div>
  );
}
