import React, { useState, useEffect, useContext } from "react";
import { useParams, useLocation } from "react-router-dom";
import { Sidebar } from "../sidebar/index";
import { DataTemplate } from "../templates/DataTemplate";
import Slider from "react-slick";
import "./models.scss";
import { Header } from "../header/index";
import { ModelListContext } from "../../contexts/ModelListContext";
import { getModelDetails } from "../../services/ModelService";

//imports for arrows of carousel
import { NextArrow, PrevArrow } from "./arrows/arrows";

export function Model(props) {
  let { id } = useParams();
  id = parseInt(id);
  let loc = useLocation();
  let location = loc.pathname;
  location = location
    .split("/")
    .slice(0, -1)
    .join("/");

  let gender;
  if (props.gender == "female") gender = "F";
  else if (props.gender == "male") gender = "M";
  else gender = "K";
  //image constants
  const all = "https://sumo-99.github.io/file-host/new_images/all.svg";
  const hair = "https://sumo-99.github.io/file-host/new_images/hair.svg";
  const shoes = "https://sumo-99.github.io/file-host/new_images/shoes.svg";
  //states
  const [active, setActive] = useState(0);
  const [name, setName] = useState(null);
  const [size, setSize] = useState(null);
  const [height, setHeight] = useState(null);
  const [poses, setPoses] = useState([]);
  const [modelList, setModelList] = useState([]);
  const [activeSlide, setActiveSlide] = useState(0);
  const [modelDetails, setModelDetails] = useState([]);
  const [displayContent, setDisplayContent] = useState(false);
  const [footwear_content, setFootwearContent] = useState([]);
  const [hairstyle_content, setHairstyleContent] = useState([]);
  const [all_content, setAllContent] = useState([]);
  //contexts
  let { models } = useContext(ModelListContext);

  const categories = [{ All: all }, { Shoes: shoes }, { "Hair Style": hair }];

  const getData = async () => {
    //get model details by passing gender as qp, and id as qs:
    console.log("get data running with id: ", id);
    let data = await getModelDetails(
      {
        gender: props.gender === "female" ? "women" : "men",
      },
      id
    );
    data = data.details;
    //pass data to state to be used in page content
    setModelDetails(data);
    setName(data.model_name);
    setSize(data.size);
    setHeight(data.height);
    setPoses(data.poses);
    //data ready => show content right of sidebar
    setFootwearContent(data.footwear);
    setHairstyleContent(data.hairstyle);
    setAllContent(data.footwear.concat(data.hairstyle));
    setDisplayContent(true);
  };

  //This runs to get the model details from BE, on mount and when we move to a different model
  useEffect(() => {
    //get the list of model ids, from context
    setModelList(models);
    //calls the api to get model data
    getData();
  }, [loc.pathname]);

  //this useEffect is to control which content shows up when a box on the sidebar is clicked
  useEffect(() => {
    generateContent();
  }, [active]);

  const generateContent = () => {
    if (active == 0) {
      return (
        <DataTemplate
          data={all_content}
          rootstyle="col-7"
          model={props.all}
          title={"All"}
          cols={3}
        />
      );
    } else if (active == 1) {
      return (
        <DataTemplate
          data={footwear_content}
          rootstyle="col-7"
          model={props.shoes}
          title={"Shoes"}
          cols={3}
        />
      );
    } else if (active == 2) {
      return (
        <DataTemplate
          data={hairstyle_content}
          rootstyle="col-7"
          model={props.hairstyle}
          title={"Hairstyle"}
          cols={3}
        />
      );
    }
  };

  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    afterChange: (curr) => {
      setActiveSlide(curr);
    },
    customPaging: (i) => {
      return (
        <div
          style={{
            width: "6.5px",
            height: "6.5px",
            backgroundColor: i == activeSlide ? "#059eef" : "#f0f0f2",
            border: "1px #f0f0f2 solid",
            borderRadius: "50%",
          }}
        ></div>
      );
    },
    nextArrow: <NextArrow />,
    prevArrow: <PrevArrow />,
  };

  return (
    <>
      <Header title={`${gender} - Model - ${name}`} modelList={modelList} />
      <div className="d-flex px-0 mx-0">
        <div className="col-3 container  mx-5 px-0 py-3 ">
          <div className="model-wrapper">
            <div className="model">
              <Slider className="text-center" {...settings}>
                {poses?.map((item, idx) => {
                  return (
                    <div key={idx}>
                      <img src={item} style={{ width: "100%" }} />
                    </div>
                  );
                })}
              </Slider>
            </div>
            <div className="py-2">
              <div>
                {gender} - Model - {name}
              </div>
              <div>Size - {size}</div>
              <div>Height - {height}</div>
            </div>
          </div>
        </div>
        <Sidebar
          rootstyle="align-self-start"
          active={active}
          setActive={setActive}
          categories={categories}
        />
        {displayContent ? generateContent() : null}
      </div>
    </>
  );
}
