import React, { useState, useEffect } from "react";
import { Link, useLocation } from "react-router-dom";
import CardCarousel from "../carousel-card/index";

export function DataTemplate(props) {
  let loc = useLocation().pathname;
  loc = loc.split("/");
  loc = loc.at(-1);

  let gender;
  if (props.gender == "female") gender = "F";
  else if (props.gender == "male") gender = "M";
  else gender = "K";

  //states
  const [data, setData] = useState([]);
  const [imglist, setImgList] = useState([]);

  useEffect(() => {
    setData(props.data);
    let temp = [];
    props.data?.forEach((item, i) => {
      item["images"].forEach((img) => {
        temp.push(img);
      });
    });
    setImgList(temp);
  }, [props.data]);

  return (
    <>
      <div className={props.rootstyle}>
        <div className="container">
          <div className="my-3">
            <div className="d-flex px-0 flex-wrap">
              {data?.map((variation, i) => {
                return (
                  <div key={i} className="col-4">
                    <CardCarousel
                      key={i}
                      images={variation["images"]}
                      height="15rem"
                      objectFit={false}
                      body={<div>{variation["item_name"]}</div>}
                    />
                  </div>
                );
              })}
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
