import React, { useState, useEffect } from "react";
import { SidebarBox } from "./SidebarBox";

export function Sidebar(props) {
  const categories = props.categories;

  const activate = (e) => {
    //fail-safe proofing
    e.stopPropagation();
    let t = e.target.parentNode.parentNode.parentNode;
    if (t.classList.contains("parent-wrapper") === false) return;

    let key = t.getAttribute("activation-key");
    props.setActive(key);
  };

  return (
    <div className={props.rootstyle}>
      <div className="container border border-2  sidebar-main px-0 ">
        <div className="col px-0">
          {props.displayTitle ? (
            <div className="text-center pt-2 pb-3 font-bold ">Categories</div>
          ) : null}
          {categories.map((item, idx) => {
            let name = Object.keys(item)[0],
              url = Object.values(item)[0];
            return (
              <div
                className="parent-wrapper my-2 py-2"
                activation-key={idx}
                key={idx}
                onClick={(e) => activate(e)}
              >
                <SidebarBox
                  name={name}
                  icon={url}
                  active={idx == props.active}
                />
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
}
