import React, { useState, useEffect } from "react";
import "./SearchBox.scss";
// import Icon from "../icon";

function SearchBox(props) {
  const [searchText, setSearchText] = useState("");

  useEffect(() => {
    setSearchText(props.value);
  }, [props.value]);

  const handleKeyDown = (e) => {
    if (e.key === "Enter") {
      props.onSearch(e.target.value);
    }
  };
  const clearSearchText = () => {
    setSearchText("");
    props.onSearch("");
  };

  return (
    <div className="searchbox mr-2 pos-relative">
      <input
        type="text"
        className="searchbox-input"
        placeholder={props.placeholder}
        value={searchText}
        onChange={(e) => setSearchText(e.target.value)}
        onKeyDown={handleKeyDown}
      />
      {searchText && (
        <div className="searchbox--search-clear" onClick={clearSearchText}>
          <img
            src="https://sumo-99.github.io/file-host/new_images/close.png"
            style={{ width: "1rem" }}
          />
        </div>
      )}
      <div
        className="searchbox--search-submit"
        onClick={() => {
          props.onSearch(searchText);
        }}
      >
        <img
          src="https://sumo-99.github.io/file-host/new_images/search.svg"
          style={{ width: "1rem" }}
        />
      </div>
    </div>
  );
}

export default SearchBox;
