import React, { useState, useEffect, useContext, useRef } from "react";
import axios from "axios";
import { Link, useLocation } from "react-router-dom";
import Card from "../card/index";
import { Header } from "../header/index";
import { ModelListContext } from "../../contexts/ModelListContext";
import Filters from "../vuecomponents/filters";
import SearchBox from "../vuecomponents/SearchBox";
import { getModelList } from "../../services/ModelService";
import Pagination from "../vuecomponents/Pagination";

const pageProps = {
  page: 1,
  page_size: 10,
};

export function ApparelTemplate(props) {
  let loc = useLocation().pathname;
  loc = loc.split("/");
  loc = loc.at(-1);

  let gender;
  if (props.gender == "female") gender = "F";
  else if (props.gender == "male") gender = "M";
  else gender = "K";

  // The below context array is used to store model IDs which are used in the model/index.js component, to move to/from next/prev model
  let { models, setModels } = useContext(ModelListContext);
  const [modelList, setModelList] = useState({
    data: [],
    totalCount: 0,
  });

  /**
   * Note: We get the list of models on component mount from the below useEffect
   * having the dependancy array ->  [filters.appliedFilter]
   */

  //to make default page props setting on component mount as it was changing if a diff page number was clicked
  useEffect(() => {
    pageProps.page = 1;
    pageProps.page_size = 10;
  }, []);

  const womenSizes = [
    { label: "XS(US2,UK6)", value: "XS" },
    { label: "S(US4/6,UK8/10)", value: "S" },
    { label: "M(US8/10,UK12/14)", value: "M" },
    { label: "L(US12/14,UK16/18)", value: "L" },
    { label: "XL(US16/18,UK20/22)", value: "XL" },
    { label: "XXL(US20,UK24)", value: "XXL" },
  ];

  const menSizes = [
    { label: "XS(US/UK34)", value: "XS" },
    { label: "S(US/UK36)", value: "S" },
    { label: "M(US/UK38)", value: "M" },
    { label: "L(US/UK40)", value: "L" },
    { label: "XL(US/UK42)", value: "XL" },
    { label: "XXL(US20,UK46)", value: "XXL" },
  ];

  const womenHeight = [
    { label: "5’1” - 5'3”", value: "5’1” - 5'3”" },
    { label: "5’4” - 5'7”", value: "5’4” - 5'7”" },
    { label: "5’8” - 5'11”", value: "5’8” - 5'11”" },
    { label: "6’ - 6'3”", value: "6’ - 6'3”" },
  ];

  const menHeight = [
    { label: "5’8” - 5'11”", value: "5’8” - 5'11”" },
    { label: "6’ - 6'3”", value: "6’ - 6'3”" },
    { label: "6'4\" - 6'7”", value: "6'4\" - 6'7”" },
    { label: "6’8\" - 6'11”", value: "6’8\" - 6'11”" },
  ];
  const appliedFiltersRef = useRef(null);
  const [filters, setFilters] = useState({
    selectedFilters: [],
    appliedFilters: [],
    applyFilter: 0,
    model_size: props.gender === "female" ? womenSizes : menSizes,
    height: props.gender === "female" ? womenHeight : menHeight,
    ethnicity: [
      { label: "African", value: "african" },
      { label: "East Asian", value: "e-asian" },
      { label: "Caucasian", value: "cauc" },
      { label: "Indian", value: "ind" },
      { label: "Mixed", value: "mixed" },
    ],
  });
  const [modelFilters, setModelFilters] = useState([]);
  const setUpInternalFilters = (options) => {
    let internalFilters = [];
    internalFilters = [
      {
        label: "Size",
        field: "model_size",
        type: "list-single-select",
        help: null,
        values: filters.model_size,
        value: filters.model_size,
        closeMenuOnBlur: false,
        show: true,
      },
      {
        label: "Height",
        field: "height",
        type: "list-single-select",
        help: null,
        values: filters.height,
        value: filters.height,
        closeMenuOnBlur: false,
        show: true,
      },
      {
        label: "Ethnicity",
        field: "ethnicity",
        type: "list-single-select",
        help: null,
        values: filters.ethnicity,
        value: filters.ethnicity,
        closeMenuOnBlur: false,
        show: true,
      },
    ];
    setModelFilters(internalFilters.filter((filter) => filter.show === true));
  };

  //filter functions:
  const setSelectedFilters = (selectedFilters) => {
    setFilters({
      ...filters,
      selectedFilters,
    });
  };

  const onApplyFilter = (appliedFilters) => {
    console.log("Apply clickde: ", appliedFilters);
    pageProps.page = 1;
    setFilters({
      ...filters,
      selectedFilters: appliedFilters,
      appliedFilters,
      applyFilter: filters.applyFilter + 1,
    });
  };

  const setAppliedFilters = (appliedFilters) => {
    setFilters({
      ...filters,
      appliedFilters,
    });
  };

  const getQs = () => {
    let queryStringArr = [];
    let filterParams = filters.appliedFilters
      .filter((sfv) => sfv.values.length > 0)
      .map((selectedFilterItem) => {
        return `${selectedFilterItem.field}=${selectedFilterItem.values.join(
          ","
        )}`;
      });

    queryStringArr = [...queryStringArr, ...filterParams];

    return `${queryStringArr.join("&")}`;
  };

  const onHandlefilter = (value, key) => {
    setFilters({
      ...filters,
      [key]: value,
      applyFilter: filters.applyFilter + 1,
    });
  };

  //to set filters:
  useEffect(() => {
    setUpInternalFilters();
  }, [filters.selectedFilters]);

  const getParams = (filterObj) => {
    let params = {};
    if (filters?.searchText?.length > 0) {
      params = {
        gender: props.gender === "female" ? "women" : "men",
        search: filters.searchText,
      };
    } else
      params = {
        page: pageProps.page,
        page_size: pageProps.page_size,
        gender: props.gender === "female" ? "women" : "men",
        search: null,
      };
    //height,ethnicity,model_size will be added from below loop
    filterObj.forEach((obj, idx) => {
      let key = obj.field,
        value = "";
      obj.values.forEach((val) => {
        value += val;
      });
      params[key] = value;
    });
    return params;
  };

  //(not filter function) function to get model list from BE
  const setUpModels = async () => {
    const qs = getQs();
    const params = getParams(filters.appliedFilters);
    //get modelList data from BE:
    let data = await getModelList(params);
    console.log("data: ", data);
    //empty condition
    if (data.models.length === 0) {
      setModelList([]);
      return;
    }
    setModelList({
      data: data.models,
      totalCount: data.totalCount,
    });
    let temp = [];
    data.models.forEach((item, idx) => {
      temp.push(item.id);
    });
    setModels(temp);
    // setTest(data.models);
  };

  // to send request when a)the component first mounts b)a filter/search is applied:
  useEffect(() => {
    setUpModels();
  }, [filters.applyFilter]);
  // end filter stuff

  //pagination functions:
  //Change page and request models for the next page:
  const changePage = (page, size) => {
    if (page) pageProps.page = page;
    if (size) pageProps.size = size;
    setUpModels();
  };
  //end pagination functions

  return (
    <>
      <Header />
      <div className="mx-5 py-1 d-flex-end flex-row">
        <div className=" col-2 d-flex justify-content-start align-items-center px-1 py-2">
          <SearchBox
            placeholder="Search models"
            value={filters.searchText}
            onSearch={(value) => {
              onHandlefilter(value, "searchText");
            }}
            searchBoxId="search-box"
          />
        </div>
        <div className="col-1 d-flex justify-content-start align-items-center px-1 py-2 mx-1">
          <Filters
            filters={modelFilters}
            selectedFilters={filters.selectedFilters}
            appliedFilters={filters.appliedFilters}
            setSelectedFilters={setSelectedFilters}
            setAppliedFilters={setAppliedFilters}
            onApplyFilter={onApplyFilter}
            appliedFiltersRef={appliedFiltersRef?.current}
          />
        </div>
      </div>
      <div className={props.rootstyle}>
        <div className="py-3">
          <div
            className="d-flex justify-content-between mt-2 p-v-2"
            style={{ marginLeft: "12px" }}
          >
            {filters.appliedFilters?.length > 0 ? (
              <div
                className={`pb-1 mt-2 bdr-b bdr-color-4 d-flex ${
                  filters.appliedFilters?.length > 0
                    ? "applied-filters-container"
                    : ""
                }`}
                ref={appliedFiltersRef}
              />
            ) : (
              <div></div>
            )}
          </div>
          <div className="d-flex px-0 flex-wrap">
            {modelList?.data?.map((item, i) => {
              return (
                <div key={i} className="card-wrapper-5">
                  <Link id="linker" to={`${item.id}`}>
                    <Card
                      key={item.id}
                      img={item.faceshot}
                      height="22rem"
                      objectFit={false}
                      width="20rem"
                      body={
                        <>
                          <div className="">
                            {gender} - Model - {item.model_name}
                          </div>
                          <div className="">Size - {item.size}</div>
                          <div className="">Height - {item.height}</div>
                        </>
                      }
                    />
                  </Link>
                </div>
              );
            })}
            {modelList.length === 0 ? (
              <div className="my-5 mx-5 text-center">
                No Model Data Available...
              </div>
            ) : null}
          </div>
        </div>
      </div>
      <Pagination
        totalRecords={modelList?.totalCount}
        recordPerPage={pageProps.pageSize}
        selectedPage={pageProps.page}
        onPageChange={(page) => {
          changePage(page, null);
        }}
        onRecordsPerPageChange={(size) => {
          changePage(null, size);
        }}
      />
    </>
  );
}
